.user-card-wrapper {
  position: relative;
  width: 100%;
  margin-top: 10px;
  margin-bottom: 10px;
  height: 509px;
  display: flex;
  flex-direction: column;
  background-color: white;
  border-radius: 5px;
  box-shadow: 0px 0px 4px 3px rgba(141, 139, 141, 0.4);
}

.user-image {
  width: 100%;
  flex-grow: 1;
  background-size: cover;
  background-position: center;
  background-repeat: no-repeat;
  border-top-left-radius: 5px;
  border-top-right-radius: 5px;
}

.user-main-info {
  padding: 16px 30px;
  display: flex;
  flex-direction: column;
}

.user-login {
  color: #bdbcbe;
  font-size: 0.9rem;
}

.user-name {
  margin-top: 5px;
  margin-bottom: 20px;
  color: #00a48c;
  font-size: 1.5rem;
}

.fa-map-marker-alt {
  margin-right: 5px;
  color: #00e2c2;
}

.user-location {
  font-size: 0.8rem;
}

.user-secondary-info {
  padding-top: 28px;
  padding-bottom: 18px;
  display: flex;
  flex-direction: row;
  justify-content: space-evenly;
  color: white;
  background: linear-gradient(#00a58d, #00e2c2);
  border-bottom-left-radius: 5px;
  border-bottom-right-radius: 5px;
}

.secondary-info-item {
  display: flex;
  flex-direction: column;
  align-items: center;
}

.sub-item-number {
  font-size: 3rem;
}

.sub-item-info {
  font-size: 0.8rem;
  font-weight: 300;
}

.time-as-user {
  position: absolute;
  padding-top: 10px;
  padding-right: 20px;
  top: 100%;
  right: 0;
  font-size: 0.9rem;
  color: #5f6264;
}